.modal-backdrop {
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: rgba(0,0,0,0.5);
}

.modal {
    padding: 30px;
    max-width: 480px;
    margin: 200px auto;
    background: #fff;
    border-radius: 10px;
    overflow: auto;
}


@media (max-width: 450px) {
        .modal{
            margin: 0;
        }
    }

@media (max-width: 280px) {
        .modal{
            margin: 0;
            max-height: 653px;
            font-size: .86em;
        }
    }

.note{
        font-size: .7em;
        text-align: center;
}