.wrapper{
    align-items: center;
    margin-left: 300px;
    margin-right: 300px;
    padding: 20px;
    border: solid 4px;
    border-color: rgb(170, 170, 24);
    border-radius: 4px;
    background-color: white;
}

@media (max-width: 1200px) {
    .wrapper{
        margin-left: auto;
        margin-right: auto;
    }
    .background{
        display: none;
    }
}



.title{
    text-align: center;
